.app {
    margin: 0 2%;
    display: flex;
}

/* shop*/
.shop {
    width: 85%;
}

/* header*/
.header{
    background-color: #1a6caa;
}
.header__title{
    height: 80px;
    padding-left: 25px;
    padding-bottom: 25px;
    font-size: 32px;
    font-weight: 600;
    border-bottom: 1.5px solid rgb(11, 174, 196);
    border-radius: 5px;
}
.header__title h1{
    color: #fff;
    line-height: 50px;
}
.header-search{
    position: relative;
    width: 200px;
}

.header-search__input{
    width: 100%;
    height: 30px;
    background: #FFFFFF;
    border: 2px solid #0abada;
    outline: none;
    box-sizing: border-box;
    border-radius: 14.4171px;
    padding-left: 10px;
    padding-right: 31px;
}

.header-search__logo{
    position:absolute;
    top:10px;
    left:169px;
    width: 30px;
    height: 30px;
    background-color: #0abada;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-menu{
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #1b75bb;
}
.header-menu__nav{
    font-size:20px;
    color: #fff;
    cursor: pointer;
}

.book{
    display: flex;
    flex-wrap: wrap;
}
.book-cover{
    margin-left: 30px;
    width: 20%;
}
.book-cover__img{
    width: 100%;
    height: auto;
}

.book-cover-title-container{
    margin: 15px 0 17px 20px;
}

.book-cover__title{
    color: inherit;
    font-size: 16px;
    margin-bottom: 4px;
}

.book-cover__name{
    color:rgba(0, 0, 0, 0.55);
    font-size: 13px;
}

.book-cover-price-container{
    display:flex;
    padding:0 13px;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.book-cover__btn{
    color: #fff;
    background-color:#26a9e0;
    border:none;
    border-radius:10px;
    height: 35px;
    font-size: inherit;
    white-space: nowrap;
}

.book-cover__btn:hover{
    background-color:#2298ca;
}

.book-cover__price{
    font-size:17px;
    color:rgba(0, 0, 0, 0.575);
    font-weight: 700;
}

/* Sidebar */
.sidebar{
    width: 200px;
    background-color: rgb(69, 7, 150);
}
.sidebar-cart{
    min-height:70px;
    font-size: 16px;
    color: white;
    background-color: rgb(100, 33, 155);
    border-radius:10px;
    margin: 80px 10px 0 10px;
    padding: 10px 0 10px 15px
}

.sidebar-cart-text{
    font-size:18px;
    margin-top: 10px;
}

.sidebar-cart__content_btn{
    width: 135px;
    height: 30px;
    color: #a675b3;
    line-height: 20px;
    display: block;
    margin: 20px 0px;
    font: normal 17px arial;
    outline:none;
}

.sidebar-cart__content_btn:not(.active) {
    box-shadow: inset 0 1px 1px rgba(111, 55, 125, 0.8), inset 0 -1px 0px rgba(63, 59, 113, 0.2), 0 9px 16px 0 rgba(0, 0, 0, 0.3), 0 4px 3px 0 rgba(0, 0, 0, 0.3), 0 0 0 1px #150a1e;
    background-image: linear-gradient(#3b2751, #271739);
    text-shadow: 0 0 21px rgba(223, 206, 228, 0.5), 0 -1px 0 #311d47;
}
  
.sidebar-cart__content_btn:not(.active):hover,
.sidebar-cart__content_btn:not(.active):focus {
    transition: color 200ms linear, text-shadow 500ms linear;
    color: #fff;
    text-shadow: 0 0 21px rgba(223, 206, 228, 0.5), 0 0 10px rgba(223, 206, 228, 0.4), 0 0 2px #2a153c;
}

a.buttons:not(:hover) {
    transition: 0.6s;
}


.sidebar-cart__content{
    border: 1px solid #cecece;
    padding: 10px;
    margin-top: 10px;
    font-size: 12px;
}

.sidebar-cart__content_close{
    display: none;
}

.sidebar-cart__content_open{
    display: block;
    margin-top: 15px;
}

.sidebar-cart__buy-text{
    font-size: 14px;
    margin-bottom: 10px;
}

.sidebar-genre{
    background-color: rgb(38, 49, 74);
    margin-top: 70px;
    padding:10px;
}
.sidebar-genre__link{
    cursor: pointer;
    margin: 10px 0;
}
.sidebar-genre__link:hover{
    background-color: rgb(91, 97, 91);
}
.sidebar-genre__link_active{
    background-color: rgb(91, 97, 91);
}
.sidebar-genre__link_icon{
    width: 6%;
    height: 10px;
    float: left;
    margin: 4px 6px 0px 20px;
    border-radius: 50%;
}
.sidebar-genre__link_item{
    font-size: 18px;
    color: rgb(120, 130, 157);
    padding-bottom: 4px;
}
.sidebar__contact{
    font-size: 12px;
    color: white;
    margin: 10px 10px;
}

.sidebar__contact-text{
    cursor: pointer;
    margin-bottom: 10px;
}

.sidebar__contact-phone, .sidebar__contact-email{
    font-size:15px;
    margin-bottom: 10px;
}

.connect__web3{
    width: 150px;
    height:35px;
    margin-top: 20px;
    border: none;
}